    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="tongGuo(1)"> </el-button>
      <el-button type="danger" @click="innerVisible = true"> </el-button>
    </div>
    <el-dialog width="30%" title="" :visible.sync="innerVisible" append-to-body>
      <el-form :model="form">
        <el-form-item label="">
          <el-input type="textarea"  placeholder="" v-model="form.remark">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="danger" @click="tijiao(-1)"> </el-button>
      </div>
    </el-dialog>
  </el-dialog> -->
</div>
</template>
<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

.el-table .error-row {
  background: #efbdc6;
}
</style>
<script>
export default {
  data() {
    return {
      innerVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible: false,
      form: {},
      kaiPiao: {},
      tableLoading2: false,
      auditItem: "",
      activeName: 'first',
      username: '',
      name: '',
      uid: '',
      tableLoading: false,
      currentPage0: 1,
      totalnum0: 0,
      pagesize0: 10,
      currentPage1: 1,
      totalnum1: 0,
      pagesize1: 10,
      currentPage2: 1,
      totalnum2: 0,
      pagesize2: 10,

      tableData0: [],
      tableData1: [],
      tableData2: [],

    }
  },
  methods: {
    toShenHe(item) {
      this.dialogFormVisible2 = true;
      this.kaiPiao = item;
    },
    tijiao(status){
      // let params = {
      //   id: this.kaiPiao.id,
      //   status: status,
      //   auditId: this.uid,
      //   auditName: this.name,
      //   addUserId: this.auditItem.addUserId,
      //   auditRemark: this.form.remark
      // }
      this.kaiPiao.auditShangName = this.name;
      this.kaiPiao.auditShangId = this.uid;
      this.kaiPiao.status = status;
      this.kaiPiao.shangWuRemark=this.form.remark
      this.postRequest("/kai/piao/check", this.kaiPiao).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.dialogFormVisible2 = false;
          this.innerVisible=false;
          this.getCollectMouldList(0, this.currentPage0, this.pagesize0)
          //this.tongGuoShenHe(0);
        } else {
          this.$message.error("");
        }
      })
    },
    tongGuo(status) {
      this.kaiPiao.auditShangName = this.name;
      this.kaiPiao.auditShangId = this.uid;
      this.kaiPiao.status = status;
      this.postRequest("/kai/piao/check", this.kaiPiao).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.$message.success("");
          this.dialogFormVisible2 = false;
          this.getCollectMouldList(0, this.currentPage0, this.pagesize0)
          //this.tongGuoShenHe(0);
        } else {
          this.$message.error("");
        }
      })
    },
    toAudit(item) {
      this.auditItem = item;
      this.dialogFormVisible = true;
      this.getRequest("/fa/mo/info?id=" + item.id).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.form = resp.data.data.faMo
        }
      })
    },
    tongGuoShenHe(item) {
      if (item.index == 0) {
        this.getCollectMouldList(0, this.currentPage0, this.pagesize0)
      } else if (item.index == 1) {
        this.getCollectMouldList(1, this.currentPage1, this.pagesize1)
      } else if (item.index == 2) {
        this.getCollectMouldList(-1, this.currentPage2, this.pagesize2)
      }

    },
    tableRowClassName({
      row,
      rowIndex
    }) {
      return 'warning-row';
    },
    tableRowClassName1({
      row,
      rowIndex
    }) {
      return 'success-row';
    },
    tableRowClassName2({
      row,
      rowIndex
    }) {
      return 'error-row';
    },

    handleSizeChange0(val) {
      this.pagesize0 = val;
      this.getCollectMouldList(0, this.currentPage0, this.pagesize0);
    },
    handleCurrentChange0(val) {
      this.currentPage0 = val;
      this.getCollectMouldList(0, this.currentPage0, this.pagesize0);
    },

    handleSizeChange1(val) {
      this.pagesize1 = val;
      this.getCollectMouldList(1, this.currentPage1, this.pagesize1);
    },
    handleCurrentChange1(val) {
      this.currentPage1 = val;
      this.getCollectMouldList(1, this.currentPage1, this.pagesize1);
    },

    handleSizeChange2(val) {
      this.pagesize2 = val;
      this.getCollectMouldList(-1, this.currentPage2, this.pagesize2);
    },
    handleCurrentChange2(val) {
      this.currentPage2 = val;
      this.getCollectMouldList(-1, this.currentPage2, this.pagesize2);
    },

    // 
    getCollectMouldList(audit, currentPage, pagesize) {
      let _this = this
      this.getRequest("/kai/piao/listbypage?page=" + currentPage + "&size=" + pagesize + "&status=" + audit + "&type=2").then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          console.log(resp);
          if (audit == 0) {
            _this.tableData0 = resp.data.data.famolist
            _this.totalnum0 = resp.data.data.count
          } else if (audit == 1) {
            _this.tableData1 = resp.data.data.famolist
            _this.totalnum1 = resp.data.data.count
          } else if (audit == -1) {
            _this.tableData2 = resp.data.data.famolist
            _this.totalnum1 = resp.data.data.count
          }
        }
      })
    }
  },
  mounted: function() {
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    this.getCollectMouldList(0, 1, 10);
  }
}
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.el-select .el-input {
  width: 130px;
}

.card-bottom {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: right;
}

.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

.card-bottom-left {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: left;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.mould-card {
  margin-bottom: 15px;
}

.mould-card:hover {
  transform: translateY(-2%);
  box-shadow: 1px 4px 10px 2px #CCC;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.pre-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
}
</style>



